body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
.navbar li {
    display: inline-block;
    text-align: center;
  }
  
  .navLeft {
    width: 15%;
  }
  
  .navCenter {
      width: 70%;
  }
  
  .navRight {
    width: 15%;
  }
  
  .navbar {
    background: #090549;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }
  
  li {
    color: white;
    padding: 15px 0;
  }
  
  body {
    font-size: 24px;
  }
.header{
	background-color: rgb(97, 141, 170);
	position: relative;
	width: 100%;
}

h3{
	text-align: center;
	font-size: 40px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 20px;
	padding-bottom: 40px;
}
.card {
    width: 200px;
    height: 150px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }
.footer{
	background-color: #090549;
	width: 100%;
	position: relative;
}

.footer p{
	color: white;
	text-align: center;
	padding: 10px 0;
	font-size: 15px;
}
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}
